h1, h2, h3, h4, h5, h6, p, ul, address, button {
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
  display: block;
}

li {
  list-style-type: none;
}

.supersoft-bg {
  background: var(--gradient-Supersoft);
  border-radius: 0 0 12px 12px;
  margin-bottom: 80px;
  position: relative;
}

.supersoft-bg:before, .supersoft-bg:after {
  content: "";
  height: 10px;
  border-radius: 0 0 12px 12px;
  display: block;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.supersoft-bg:before {
  width: calc(100% - 40px);
  background: #cacadb;
  bottom: -10px;
}

.supersoft-bg:after {
  width: calc(100% - 80px);
  background: #e7e7f2;
  bottom: -20px;
}

body.scroll-disable {
  overflow-y: hidden;
}

.page-container {
  background-color: #fff;
  border-radius: 5px;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: 2rem;
  padding: 10px 15px;
  display: flex;
  box-shadow: 0 0 10px #d3d3d3;
}

.pagination {
  gap: 5px;
  display: flex;
}

.pg-link {
  pointer-events: none;
  color: #111321;
  font-family: Manrope;
  font-size: 14px;
  font-weight: 500;
  line-height: 19px;
  text-decoration: none;
}

.pg-item {
  width: 28px;
  height: 28px;
  cursor: pointer;
  background-color: #f4f4f4;
  border: 1px solid #4440f6;
  border-radius: 2px;
  justify-content: center;
  align-items: center;
  display: flex;
  overflow: hidden;
}

.pg-item.active, .pg-item:hover, .pg-item:focus {
  background-color: #4440f6;
}

.header {
  min-height: 81px;
  border-bottom: 1px solid rgba(0, 0, 0, .2);
  transition: border-color 1s;
}

body.darkmode .header {
  border-color: rgba(0, 0, 0, .2);
}

.header__box {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.header__thumb {
  align-items: center;
  margin-left: auto;
  display: flex;
}

@media screen and (min-width: 1280px) {
  .header__thumb {
    gap: 147px;
  }
}

@media screen and (min-width: 768px) {
  .header__nav {
    margin-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .header__nav {
    margin-right: 0;
  }
}

.header__search-container {
  align-items: center;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header__search-container {
    margin-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .header__search-container {
    margin-right: 0;
  }
}

.is-open {
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

@media screen and (min-width: 768px) {
  .is-open {
    display: none;
  }
}

.btn-open__icon {
  stroke: var(--color-primary);
  margin-left: 14px;
}

body.darkmode .btn-open__icon {
  stroke: var(--color-bg-primary);
}

.menu {
  display: none;
}

@media screen and (min-width: 768px) {
  .menu {
    gap: 44px;
    display: flex;
  }
}

@media screen and (min-width: 1280px) {
  .menu {
    gap: 69px;
  }
}

.menu__link {
  font-weight: 700;
  transition: color .25s;
}

@media screen and (min-width: 768px) {
  .menu__link {
    position: relative;
  }
}

@media screen and (min-width: 768px) {
  .menu__link:after {
    content: "";
    background-color: var(--color-accent);
    width: 100%;
    height: 1px;
    transition: transform .25s;
    position: absolute;
    bottom: -5px;
    left: 0;
    transform: scaleX(0);
  }
}

.menu__link:hover, .menu__link:focus {
  color: var(--color-accent);
}

.menu__link:hover:after, .menu__link:focus:after {
  transform: scaleX(1);
}

.search-form {
  background-color: rgba(0, 0, 0, 0);
  display: flex;
}

@media screen and (max-width: 767.98px) {
  .search-form:hover {
    border: 1px solid var(--color-primary);
    border-radius: 20px;
    outline: none;
    margin-top: 1px;
  }

  .search-form:hover input {
    width: 125px;
    padding: 8px 0;
  }

  .search-form:hover .search-form__btn {
    border: none;
  }
}

@media screen and (min-width: 768px) {
  .search-form {
    width: 213px;
    border: 1px solid;
    border-radius: 20px;
  }
}

@media screen and (min-width: 1280px) {
  .search-form {
    width: 288px;
    margin: 0 auto;
  }
}

.search-form input {
  font-family: var(--font-primary);
  letter-spacing: .02em;
  color: var(--color-primary);
  background: none;
  border: none;
  outline: none;
  font-size: 12px;
  line-height: 1.75;
  transition: all .4s;
}

@media screen and (max-width: 767.98px) {
  .search-form input {
    width: 0;
    padding: 0;
    transition: all .4s;
  }
}

@media screen and (min-width: 768px) {
  .search-form input {
    padding: 5px 0;
    font-size: 14px;
    line-height: 1.71429;
  }
}

body.darkmode .search-form input {
  color: var(--color-bg-primary);
}

.search-form__btn {
  background-color: inherit;
  border: none;
  padding: 6px 16px 6px 12px;
  transition: color 1s;
}

@media screen and (max-width: 767.98px) {
  .search-form__btn {
    color: var(--color-primary);
  }
}

body.darkmode .search-form__btn {
  color: var(--color-bg-primary);
  transition: color 1s;
}

.mobile-menu {
  width: 100%;
  height: 100%;
  background-color: var(--color-bg-primary);
  z-index: 5;
  transition: all .25s cubic-bezier(.12, .56, .79, .6);
  position: fixed;
  top: 0;
  left: 0;
  transform: translateY(-100%);
}

.mobile-menu.is-open {
  transition: all .25s cubic-bezier(.12, .56, .79, .6);
  transform: translateY(0);
}

@media screen and (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}

body.darkmode .mobile-menu {
  background-color: var(--color-bg-inversed);
}

.mobile-menu__wrapper {
  align-items: center;
  margin-bottom: 68px;
  display: flex;
}

.menu-close {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin-left: auto;
}

.menu-close svg {
  stroke: var(--color-primary);
  fill: var(--color-primary);
}

.icon-wrapper {
  width: 32px;
  height: 32px;
  background-color: var(--color-accent);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.icon-wrapper--current {
  border: 1px solid var(--color-bg-primary);
}

.mobile-list {
  flex-direction: column;
  gap: 24px;
  display: flex;
}

.mobile-list__link {
  align-items: center;
  gap: 14px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.375;
  display: flex;
  position: relative;
}

.mobile-list__link:hover, .mobile-list__link--current {
  height: 44px;
  width: 100%;
  background-color: var(--color-accent);
  box-shadow: 0 0 0 100vmax var(--color-accent);
  clip-path: inset(0 -100vmax);
  color: var(--color-bg-primary);
}

.current-page__icon {
  fill: var(--color-bg-primary);
  position: absolute;
  right: -1px;
  transform: rotate(270deg);
}

.mobile-list__icon {
  fill: var(--color-accent);
  stroke: var(--color-bg-primary);
}

.logo {
  font-size: 42px;
  font-weight: 700;
  line-height: 57px;
}

.toggle-mode {
  width: 98px;
  height: 21px;
  align-items: center;
  gap: 8px;
  display: flex;
}

@media screen and (max-width: 767.98px) {
  .toggle-mode {
    opacity: 0;
    pointer-events: none;
    visibility: hidden;
    display: none;
  }

  .toggle-mode.mobile {
    z-index: 6;
    opacity: 1;
    pointer-events: all;
    visibility: inherit;
    display: flex;
    position: absolute;
    bottom: 18px;
  }
}

@media screen and (min-width: 1280px) {
  .toggle-mode {
    width: 148px;
    height: 24px;
    gap: 12px;
  }
}

@media screen and (min-width: 1280px) {
  .toggle-mode__icon {
    display: none;
  }
}

.sun {
  fill: rgba(0, 0, 0, 0);
  stroke: var(--color-accent);
}

body.darkmode .sun, .moon {
  fill: rgba(0, 0, 0, 0);
  stroke: #5f6775;
}

body.darkmode .moon {
  fill: rgba(0, 0, 0, 0);
  stroke: var(--color-accent);
}

.toggle-mode__switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.toggle-mode__checkbox {
  display: none;
}

.toggle-mode__slider {
  cursor: pointer;
  border: 1px solid var(--color-accent);
  background-color: var(--color-bg-primary);
  transition: all .5s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.toggle-mode__slider:before {
  content: "";
  height: 16px;
  width: 16px;
  background-color: var(--color-accent);
  transition: all .5s;
  position: absolute;
  bottom: 1px;
  left: 1px;
}

.toggle-mode__slider.round {
  border-radius: 34px;
}

.toggle-mode__slider.round:before {
  border-radius: 50%;
}

input:checked + .toggle-mode__slider:before {
  background-color: var(--color-bg-inversed);
}

input:checked + .toggle-mode__slider {
  background-color: var(--color-accent);
  border: 1px solid var(--color-bg-inversed);
}

input:focus + .toggle-mode__slider {
  box-shadow: 0 0 1px var(--color-accent);
}

input:checked + .toggle-mode__slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(20px);
}

.toggle-mode__dark {
  display: none;
}

@media screen and (min-width: 1280px) {
  .toggle-mode__dark {
    color: #5f6775;
    font-size: 18px;
    display: block;
  }

  body.darkmode .toggle-mode__dark {
    color: var(--color-accent);
  }
}

.toggle-mode__light {
  color: var(--color-accent);
  display: none;
}

@media screen and (min-width: 1280px) {
  .toggle-mode__light {
    font-size: 18px;
    display: block;
  }

  body.darkmode .toggle-mode__light {
    color: var(--color-bg-primary);
    filter: opacity(25%);
  }
}

.articles {
  background: #f4f4f4;
  flex-wrap: wrap;
  align-items: center;
  gap: 28px;
  margin: 0 auto;
  display: flex;
}

.articles__item {
  width: 288px;
}

@media screen and (min-width: 768px) {
  .articles__item {
    width: 353px;
  }
}

@media screen and (min-width: 1280px) {
  .articles__item {
    width: 394px;
  }
}

.articles__image-wrapper {
  position: relative;
}

.articles__image {
  width: 100%;
  height: 395px;
  object-fit: cover;
  border-radius: 4px;
  display: block;
}

.articles__category {
  text-align: center;
  color: #fff;
  background-color: rgba(68, 64, 246, .698);
  border-radius: 0 4px 4px 0;
  padding: 4px 8px;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  position: absolute;
  top: 40px;
}

.articles__button {
  text-align: center;
  color: #111321;
  cursor: pointer;
  background: #fff;
  border-radius: 20px;
  padding: 6px 12px;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
  position: absolute;
  bottom: 12px;
  right: 12px;
